@import './Home.scss';
@import './font.scss';
@import './knt.scss';
body{
    font-family: 'Press Start 2P', cursive;
    font-size: 16px;
    line-height: 21px;
    color: #d1fb84;
    outline-color: #d1fb84;
    text-shadow: 
    0 0 30px #d1fb84,
    0 0 50px #d1fb84;
}
.app{
    background: url(../images/bg-pixel.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    position: relative;
    &::after{
        content: 'KHT Product';
        position: absolute;
        font-family: 'Black Ops One', cursive;
        color: #364859;
        font-size: 21px;
        line-height: 24px;
        display: flex;
        justify-content: center;
        top: 60px; left: 0; right: 0;
        outline-color:none;
        text-shadow: none;
        z-index: 3;
    }
}
.pc{
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 2;
    &::after{
        content: '';
        position: absolute;
        left: 0; bottom: 0; top: 0; right: 0;
        margin: auto;
        width: 830px;
        height: 680px;
        background: #0F0F0F;
        z-index: -1;
    }
    &::before{
        content: '';
        position: absolute;
        background: url(../images/pc-pixel.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        left: 0; bottom: 0; top: 0; right: 0;
        margin: auto;
        z-index: 2;
    }
}
.container{
    display: block;
    max-width: 800px;
    height: 100vh;
    margin: 0 auto;
    padding: 200px 20px;
    position: relative;
    z-index: 3;
}

.line {
    position: absolute;
    width: 102%;
    height: 640px;
    top: 155px;
    left: -10px;
    border-radius: 60px;
    overflow: hidden;
    &::after {
        content: '';
        display: block;
        position: absolute;
        height: 50vh;
        width: 100%;
        top: -100%;
        left: 0;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #d1fb84 500%);
        animation-name: drop;
        animation-duration: 7s;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        opacity: 0.2;
    }
}

@keyframes drop {
    0% {
        top: -100%
    }

    100% {
        top: 500%
    }
}

@media (max-width:1060px){
    body{
        background-color: #0F0F0F;
    }
    .line{
        display: none;
    }
    .app{
        background: #0F0F0F;
        height: 100vh;
        width: 100%;
        position: relative;
        &::after{
            display: none;
        }
    }
    .pc{
        width: 100%;
        height: 100vh;
        position: relative;
        z-index: 2;
        &::after{
           display: none;
        }
        &::before{
           display: none;
        }
    }
    .container{
        display: block;
        max-width: 800px;
        height: 100vh;
        margin: 0 auto;
        padding: 50px 20px;
        position: relative;
        z-index: 3;
    }
}