.knt {

    &__sub-item {
        text-align: left;
        margin-top: 30px;
        padding-left: 50px;
        font-size: 16px;
        font-weight: 400;
        transition: all 0.3s;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            font-size: 16px;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto 0;
            text-shadow:
                0 0 30px #d1fb84,
                0 0 30px #d1fb84;
            z-index: 1;
        }

        &:nth-child(1) {
            margin-top: 60px;
            &::after {
                content: '1.';
            }
        }

        &:nth-child(2) {
            &::after {
                content: '2.';
            }
        }

        &:nth-child(3) {
            &::after {
                content: '3.';
            }
        }

        &:nth-child(4) {
            &::after {
                content: '4.';
            }
        }

        &:nth-child(5) {
            &::after {
                content: '5.';
            }
        }

        &:nth-child(6) {
            &::after {
                content: '6.';
            }
        }
    }

    &__sub-item--even {
        position: relative;
        &::after {
            position: absolute;
            content: '00';
            font-size: 9px;
            line-height: 8px;
            left: 10px;
            top: -10px;
        }
    }

    &__sub-item--neven {
        position: relative;
        &::after {
            position: absolute;
            content: '01';
            font-size: 9px;
            line-height: 8px;
            left: 10px;
            top: -10px;
        }
    }

    &__prepod {
        font-size: 13px;
        line-height: 13px;
    }

    &__btns {
        margin-top: 30px;
        display: flex;
        gap: 25px;
        z-index: 4;
    }

    &__btn {
        border: none;
        background: transparent;
        outline: none;
        color: #d1fb84;
        outline-color: #d1fb84;
        text-shadow:
            0 0 30px #d1fb84,
            0 0 50px #d1fb84;
        cursor: pointer;
        z-index: 4;
    }
    &__zero{
        display: flex;
        justify-content: space-between;
        margin: 0; padding: 0 10px;
        font-size: 12px;
        line-height: 12px;
        font-weight: 400;
        gap: 30px;
    }
    &__zero-text--two{
        margin-left: auto;
    }
    &__btns-t{
        position: absolute;
        right: 0;
        bottom: 165px;
        display: flex;
        gap: 25px;
        justify-content: flex-end;
        padding-right: 60px;
    }
    &__btn-next, &__btn-prev , &__btn-home , &__btn-download{
        border: 1px solid #d1fb84;
        background: transparent;
        outline: none;
        font-size: 13px;
        color: #d1fb84;
        outline-color: #d1fb84;
        text-shadow:
            0 0 30px #d1fb84,
            0 0 50px #d1fb84;
        cursor: pointer;
        box-shadow:
            0 0 5px #d1fb84,
            0 0 30px #d1fb84;
        transition: all 0.3s;
        padding: 8px 12px;
        &:hover{
            color: #0f0f0f;
            background-color: #d1fb84;
        }
    }
    &__btn-prev , &__btn-next , &__btn-download{
        font-size: 21px;
        padding: 8px 10px;
    }
    &__content-box{
        display: flex;
        justify-content: space-between;
    }
    &__img-box{
        margin-top: 30px;
    }
    &__content{
        display: none;
    }
    &__active-content{
        display: block;
    }
}

@media (max-width:1060px){
    .knt{
        &__inner{
            padding-top: 50px;
            padding-bottom: 30px;
        }
        &__zero{
            display: flex;
            justify-content: space-between;
            margin: 0; padding: 0;
            font-size: 12px;
            line-height: 18px;
            font-weight: 400;
            gap: 30px;
        }
        &__btns {
            margin-top: 30px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 25px;
            z-index: 4;
        }
        &__content-box{
            display: block;
        }
        &__img-box{
            display: none;
        }
        &__btns-t{
            position: absolute;
            right: 0; left: 0; top: 20px; bottom: 0;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            gap: 25px;
            padding: 0;
        }

    }
}