.home {
    &__inner {
        display: flex;
        padding: 30px 0;
        gap: 50px;
    }
    &__text-box{
        z-index: 5;
    }
    &__title {
        font-size: 25px;
        font-weight: 400;
        line-height: 32px;
        margin-bottom: 10px;
    }

    &__sub-title {
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
        margin-bottom: 35px;
    }

    &__btns {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 30px;
    }

    &__btn {
        text-decoration: none;
        color: #d1fb84;
        font-size: 14px;
        border: 1px solid #d1fb84;
        text-align: center;
        padding: 15px 0;
        box-shadow:
            0 0 5px #d1fb84,
            0 0 20px #d1fb84;
        transition: all 0.3s;
        &:hover{
            color: #0f0f0f;
            background-color: #d1fb84;
        }
    }
    &__img-box{
        display: flex;
        align-items: center;
    }
}

@media (max-width:1060px){
    .home{
        &__inner {
            display: block;
            padding: 30px 0;
            gap: 0;
        }
        &__img-box{
           display: none;
        }
    }
}